* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #ccc;
}
#html {
  word-break: break-all;
  padding: 10px;
}
#div1 {
  position: fixed;
  right: 50px;
  top: 50px;
}
@media (max-width: 500px) {
  #div1 {
    position: fixed;
    top: 50px;
    left: 25%;
  }
}
#div1::before {
  content: "";
  display: block;
  position: absolute;
}
#div1::after {
  content: "";
  display: block;
  position: absolute;
}
@media (max-width: 500px) {
  #html {
    height: 50vh;
    overflow: auto;
  }
  #div1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
